.page {
  justify-content: center;
  align-items: center;
  height: 100vmin;
  margin: 0;
  padding: 0;
  display: flex;
}

.watch {
  box-sizing: border-box;
  border: 6px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.watch__center {
  background-color: #f3a319;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: relative;
}

.watch__center:before {
  content: "";
  z-index: -2;
  transform-origin: bottom;
  background-color: green;
  width: 10px;
  height: 240px;
  animation: 5s linear infinite move;
  display: flex;
  position: absolute;
  bottom: 15px;
}

@keyframes move {
  0% {
    transform: rotate(1deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.watch__center:after {
  content: "";
  z-index: -1;
  transform-origin: 0;
  background-color: #00f;
  width: 120px;
  height: 15px;
  animation: 60s linear infinite move;
  display: flex;
  position: absolute;
  left: 15px;
}

